._best_doc._news {
  background: #f9fcfc;
  padding: 4rem 0 !important;
}
._best_doc._news .slider_news {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
._best_doc._news .slider_news .arrows {
  display: flex;
  width: 70%;
  padding-bottom: 2rem;
  justify-content: flex-end;
}
._best_doc._news .slider_news .slider_doc {
  width: 360px !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}
._best_doc._news .slick-track .slick-slide {
  width: 360px !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  margin: 0 2rem;
}
._best_doc._news .slider_news .blur_field {
  position: absolute;

  width: 200px;
  height: 300px;
  top: 50%;
  background-color: rgb(255, 255, 255);
  z-index: 10;
  filter: blur(200px);
  -webkit-filter: blur(200px);
}
._best_doc._news .slider_news .blur_field.right {
  right: 0;
}
._best_doc._news .slider_news .blur_field.left {
  left: 0;
}
._best_doc._news .news_btn {
  background-color: white;
  border: 1px solid var(--lighter-bleu);
  color: var(--lighter-bleu);
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  border-radius: 5px;
  width: 190px;
  height: 40px;
  cursor: pointer;
}
._best_doc._news .news_btn .icon {
  background-color: white !important;
  padding: 0 1rem;
}
._best_doc._news .icon.button {
  padding: 0 !important;
}
.under_new_slider {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  width: 100%;
}
.under_new_slider a {
  position: absolute;
  right: 0;
  font-size: 14px;
  font-weight: 500;
  color: var(--lighter-bleu);
  text-decoration: underline;
  z-index: 12;
}
@media (max-width: 999px) {
  ._news .info .plus_title {
    display: none !important;
  }
  .under_new_slider a {
    position: relative !important;
    text-align: center;
  }
  .under_new_slider {
    flex-direction: column;
    width: 85%;
  }
  ._best_doc._news .icon.button {
    margin-bottom: 1.5rem;
  }
  ._best_doc._news .slider_news .blur_field.right,
  ._best_doc._news .slider_news .blur_field.left {
    display: none;
  }
}
@media (max-width: 399px) {
  ._best_doc._news .slider_news .slider_doc {
    width: 260px !important;
  }
}
